import './App.css';
import TodoList from './TodoList';
import React, { useState, useRef, useEffect } from 'react';
import uudidv4 from 'uuid/dist/v4'

function App() {
  const todoNameRef= useRef()
  const [Todos,SetTodos] =  useState([])
   
const LOCAL_STORAGE_KEY='todoApp.todos'
  useEffect(() => {
   const storedTodos=JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY))
   if(storedTodos) SetTodos(storedTodos)
  
  }, [])

useEffect(() => {
    localStorage.setItem(LOCAL_STORAGE_KEY,JSON.stringify(Todos))
  }, [Todos])


    function handleAddTodo(e)
    {
      
      const name = todoNameRef.current.value
      todoNameRef.current.value=""
        if(name==="")return
        SetTodos((prevTodos)=>{
          return[
            ...prevTodos,{id:uudidv4(),name:name,isChecked:false}
          ]
          
        })
     
    }
    function toggleTodo(id)
    {
      const newTodos=[...Todos]
      const todo= newTodos.find(todo => todo.id===id)
      console.log(`Id: ${todo.id}`)
      console.log(todo)
      todo.isChecked=!todo.isChecked
      SetTodos(newTodos)
    }
    
    function handleClear()
    {
      const newTodos=Todos.filter(todo=>!todo.isChecked)
      SetTodos(newTodos)
    }

  return (<>
  {console.log("Hello world")}
  <TodoList Todos={Todos}  toggleTodo={toggleTodo}/>
  <input ref={todoNameRef} type="text"></input>
  <button onClick={handleAddTodo} >Add Todo</button>
  <button onClick={handleClear}>clear Todo</button>
  <div>{Todos.filter(todo=>!todo.isChecked ).length} left to do</div>
  </>)
}

export default App;
